footer
{
    border-top: 1px solid black;
}
.footer-first
{
    @extend .regualr-body-m-16-;
    display: flex;
    flex-direction: column;
    text-align: left;
   
    padding: 2rem;
   li {
          @extend .regualr-body-m-16-;
           list-style-type: none;}
   h3{
    @extend .regular-heading-m-16-;
    font-weight: 600;
    
   }

   a{
    color: black;
   }

}

.img-logo{
    padding-right: 1rem;
   
    
    li{
        padding-right: 2rem;
        display: inline;
    }
}
